*{
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}
nav{
  background-color: rgb(11, 78, 78);
  color: #e7e0e0;
  padding: 20px;
  text-align: center;
}
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap');
.container{
  background-color: rgb(11, 71, 71);
  width: 70%;
  margin: auto;
  min-height: 100vh;
  padding: 4vmax 8vmax;
  box-sizing: border-box;
}
.container > form {
   display:flex;
   flex-direction: column;
}
.container > h1{
  text-align: center;
  margin: 30px;
}
input,textarea,button{
  padding:20px;
  border:1px solid #272222;
  border-radius: 5px; 
  margin: 10px 0;
}
button{
  padding: 20px;
  border:1px;
  cursor: pointer;
  background-color: rgb(48, 126, 150);
  border-radius: 5px;
  margin:10px;
}
.Task{
  align-items: center;
  display: flex; 
  background-color: white;
  padding: 5px;
  margin: 10px;
}
.Task > div {
  width: 100%;
}
.Task  button {
   border-radius: 100%;
   height: 30px;
   width: 30px;
   display: flex;
   font-size: 1.6rem;
   align-items: center;
  justify-content: center;
}